<template>
	<view class="login">
		<view class="img">
			员工端
			<image src="" mode=""></image>
		</view>
		<view class="login-input">
			<input type="test" v-model="Key" name="" id="" disabled="true" placeholder="请点击右侧扫码登录" placeholder-class="placeholder">
			<view class="imgs" @click="headleSweep">
				<image  src="/static/img/icon_add-edit.png"></image>
			</view>
		</view>
		<button @click="login" class="btn">登录</button>
	</view>
</template> 

<script>
	import { token } from '@/apis/api';
	import { LoginAjax } from '@/apis/api/modules/user'
	
	export default{
		name: 'login',
		data(){
			return{
				formData: {
					username: ''
				},
				Key: ''
			}
		},
		onLoad() {
			this.checkLogin()
		},
		methods: {
			async checkLogin() {
				const result = await this.$store.dispatch('user/getUserData')
				if (result) {
					uni.reLaunch({
						url: '/pages/index/index'
					})
				}
			},
			/**
			 * 扫码
			 */
			async headleSweep() {
				uni.scanCode({
					success: async (res) => {
						// this.Key = res.result
						this.Key = 'E06DB4CD30897BAC0ABC10A0951E19C81DBA036E7BED649947E14764EEF93563B1EBA0DECE5A1CA4B8476CB7F9814144'
						// const login = await LoginAjax({
						// 	// Key:res.result,
						// 	Key:'E06DB4CD30897BAC0ABC10A0951E19C81DBA036E7BED649947E14764EEF93563B1EBA0DECE5A1CA4B8476CB7F9814144'
						// })
					}
				})
			},
			/* *
			 * 执行登录
			 */
			async login() {
				const formData = this.formData
				
				// if (!this.validForm(formData, [
				// 	{
				// 		key: 'username',
				// 		text: '请输入账号'
				// 	}
				// ])) {
				// 	return false
				// }
				
				try{
					uni.showLoading({
						title: '登录中',
						mask: true
					})
					
					const loginResult = await LoginAjax({
						// Key: Key
						Key:'E06DB4CD30897BAC0ABC10A0951E19C81DBA036E7BED649947E14764EEF93563B1EBA0DECE5A1CA4B8476CB7F9814144'
					}, {
						header: {
							'content-type': 'application/x-www-form-urlencoded'
						}, 
					})
					
					uni.showToast({
						title: '登录成功!',
						mask: true
					})
					console.log('登录成功!',loginResult)
					// 登录成功设置token
					token.setValue(loginResult)
					// 获取用户信息存储到vuex
					// console.log(loginResult)
					await this.$store.dispatch('user/getUserData')
					
					setTimeout(() => {
						// 执行登录后逻辑，跳转到对应的页面
						// this.$store.dispatch('user/toBusinessCenterPage')
						uni.reLaunch({
							url: '/pages/index/index'
						})
						// uni.navigateBack({
						// 	delta: 1
						// })
					}, 1000)
				}catch(e){
					//TODO handle the exception
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	.login {
		height: 100%;
		overflow: hidden;
	}
	
	.img {
		width: 510upx;
		height: 164upx;
		margin: 48upx auto 40upx;
		text-align: center;
		image {
			width: 100%;
			height: 164upx;
		}
	}

	.identity {
		padding: 0 15upx;
	}

	.identitys {
		padding: 0 0 18upx 18upx;
		width: 48upx;
		height: 34upx;
		font-size: 24upx;
		font-weight: 500;
		color: #333333;
		line-height: 34upx;
	}

	.active {
		width: 210upx;
		height: 64upx;
		background: rgba(23, 71, 178, 0.1);
		border-radius: 8upx;
		border: 2upx solid #1747B2;
		text-align: center;
		line-height: 64upx;
	}

	.identity-name {
		display: flex;
		justify-content: space-around;

		.views {
			width: 210upx;
			height: 64upx;
			background: #FFFFFF;
			border-radius: 8upx;
			border: 2upx solid #E5E6ED;
			text-align: center;
			line-height: 64upx;
		}
	}
	label{
		display: block;
		padding: 30upx 0 0;
		font-size: 24upx;
		font-weight: 500;
		color: #333333;
		line-height: 34upx;
	}
	.login-input {
		padding: 0 30upx;
		display: flex;
		justify-content: center;
		align-items: center;
		.test{
			width: 500upx;
			height: 60upx;
			border: 4upx solid #EFEFEF;
			margin-right: 30upx;
		}
		.imgs{
			background-color: #1747B2;
			width: 60upx;
			height: 60upx;
			image{
				width: 60upx;
				height: 60upx;
				filter: grayscale(1) brightness(2);
			}
			
		}
	}

	input {
		padding: 20upx 0 30upx;
		border-bottom: 2upx solid #EFEFEF;
		font-size: 32upx;
		font-weight: 400;
	}
	.btn{
		margin-top: 40upx;
		width: 690upx;
		height: 100upx;
		background: #1747B2;
		border-radius: 50upx;
		font-size: 32upx;
		font-family: PingFangSC-Medium, PingFang SC;
		font-weight: 500;
		color: #FFFFFF;
		line-height: 100upx;
		letter-spacing: 1px;
	}
</style>